<template>
 <div id="app" :class="typeof weather.main != 'undefined' && weather.main.temp > 16 ? 'warm' : '' ">
   <main>
     <div class="search-box">
       <input type="text" class="search-bar" placeholder="Search..." v-model="query" @keypress="fetchWeather">
     </div>

      <div class="weather-wrap" v-if="typeof weather.main != 'undefined'">
       <div class="location-box">
         <div class="location">{{ weather.name }}, {{ weather.sys.country }}</div>
         <div class="date">{{ dateBuild() }}</div>
       </div>

       <div class="weather-box">
         <div class="temp">{{ Math.round(weather.main.temp) }}°C</div>
         <div class="weather">{{ weather.weather[0].main }}</div>
       </div>
     </div>

     <div class="footer">
       <p>Done By Ahmed Bahaa 2022©</p>
     </div>
   </main>
 </div>
</template>

<script>
import { ref } from '@vue/reactivity'


export default {
  setup() {

    const api_key = ref('61fb462a8b3862a612127f8882d68804')
    const url_base = ref('https://api.openweathermap.org/data/2.5/')
    const query = ref('')
    const weather = ref({})


    const fetchWeather = (e) => {
      if (e.key == "Enter") {
        fetch(
          `${url_base.value}weather?q=${query.value}&units=metric&appid=${api_key.value}`
        )
          .then((res) => {
            return res.json();
          })
          .then(setResults);
      }
    }
    const setResults = (results) => {
      weather.value = results;
    }

    const dateBuild = () => {
    let d = new Date();
 
    let months = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
      let days = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
      ];
      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();
      return `${day}, ${date} ${month} ${year}`;

    }

    return { api_key, url_base, query, weather, fetchWeather, dateBuild}
  }
}
</script>

